<template>
  <div class="bg">
    <div class="customer">
      <div class="title">{{ customerTable.title }}</div>
      <el-divider/>
      <el-form
        class="questions"
        ref="customerForm"
        :model="customerTable"
        label-position="top"
      >
        <div
          v-for="(qa, index) in customerTable.question_answers"
          :key="index"
        >
          <el-divider v-if="index !== 0"/>
          <el-form-item
            style="text-align: left;"
            :prop="`question_answers.${index}.answers`"
            :rules="rules[qa.question.type]"
          >
            <span slot="label" class="questionTitle">
              <el-tag>{{ questionTypeMap[qa.question.type] }}</el-tag>
              {{qa.question.title}}
            </span>
            <el-input
              v-if="qa.question.type === 'FILL_IN'"
              style="width: 80%;"
              type="textarea"
              size="medium"
              rows="4"
              :disabled="!active"
              v-model="qa.answers"
            ></el-input>
            <el-radio-group
              v-else-if="qa.question.type === 'SINGLE_ANSWER_MULTIPLE_CHOICE'"
              v-model="qa.answers[0]"
            >
              <div
                v-for="(option, index) in qa.question.options"
                :key="index"
                style="margin-bottom: 10px"
              >
                {{option.value}}
                <el-radio 
                  :label="option.value" 
                  border
                  :disabled="!active"
                >
                  {{option.value}}
                </el-radio>
              </div>
            </el-radio-group>
            <el-checkbox-group
              v-else-if="qa.question.type === 'MULTIPLE_ANSWER_MULTIPLE_CHOICE'"
              v-model="qa.answers"
            >
              <div
                v-for="(option, index) in qa.question.options"
                :key="index"
                style="margin-bottom: 10px"
              >
                <el-checkbox :label="option.value" 
                  :disabled="!active"
                  border
                >{{option.value}}</el-checkbox>
              </div>
            </el-checkbox-group>
            <el-checkbox-group v-else-if="qa.question.type === 'LIST_QUESTION'">
              <div 
                v-for="(answer, answerIndex) in qa.answers" 
                :key="answerIndex"
                style="display: flex; margin-bottom: 10px">
                <el-input v-model="qa.answers[answerIndex]" type="text" />
                <el-button
                  icon="el-icon-close"
                  style="margin-left: 10px"
                  type="danger"
                  @click="removeLine(qa.answers, answerIndex)">
                  删除
                </el-button>
              </div>
              <el-button
                type="primary"
                size="small"
                @click="addLine(qa.answers)">
                增加新列
              </el-button>
            </el-checkbox-group>
          </el-form-item>
        </div>

        <el-form-item>
          <el-button type="primary" size="large" @click="saveAnswer()">保存</el-button>
          <el-button type="success" size="large" @click="submitAnswer()">提交</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
// import Question from './components/CustomerQuestion'

export default {
  name: 'Fill',
  data() {
    const rules = {
      'SINGLE_ANSWER_MULTIPLE_CHOICE': [
        {  required: true, trigger: 'change', message: '必须且只能选择一个' }
      ],
      'MULTIPLE_ANSWER_MULTIPLE_CHOICE': [
        { type: 'array', required: true, trigger: 'change', message: '至少选一个' }
      ],
      'FILL_IN': [
        { required: true, trigger: 'blur', message: '输入不可为空' }
      ],
      'LIST_QUESTION': [
        { required: true, trigger: 'blur', message: '输入不可为空' }
      ],
    }
    return {
      active: false,
      rules,
      tableId: -1
    }
  },
  mounted() {
    this.tableId = this.$route.params.tableId
    this.getFullTable(this.tableId).then(res => {
      console.log(res)
      if(res) {
        this.active = true
      }
    })
  },
  computed: {
    ...mapGetters([
      'customerTable'
    ]),
    questionTypeMap() {
      return {
        'FILL_IN': '填空',
        'SINGLE_ANSWER_MULTIPLE_CHOICE': '单选',
        'MULTIPLE_ANSWER_MULTIPLE_CHOICE': '多选',
        'LIST_QUESTION': '列表'
      }
    }
  },
  methods: {
    ...mapActions([
      'getFullTable',
      'saveAnswers',
      'submitAnswers'
    ]),
    addLine(answers) {
      answers.push({})
      console.log("添加新列")
    },
    removeLine(answers, index) {
      answers.splice(index, 1)
    },
    buildAnswers() {
      const answers = []
      for(let qa of this.customerTable.question_answers) {
        let answerContent = qa.answers
        if(!Array.isArray(answerContent)) {
          answerContent = [answerContent]
        }
        answers.push({
          fingerprint: qa.question.fingerprint,
          answers: answerContent,
        })
      }
      return answers
    },
    saveAnswer() {
      this.$refs.customerForm.validate((valid) => {
        if(valid) {
          console.log(`form valid and save answer`)
          let answers = this.buildAnswers()
          var saveForm = {
            tableId: this.tableId,
            answers: answers
          }
          this.saveAnswers(saveForm).then(res => {
            if(res) {
              this.$message.success('保存成功')
              this.$router.push({ name: 'home' })
            } else {
              this.$message.error('保存失败，请检查网络状态')
            }
          })
        } else {
          this.$message.error('采集表不可有空栏')
        }
      })
    },
    submitAnswer() {
      const buildCreateTime = () => {
        function formatDate(date) {
          let year = date.getFullYear();
          // 注意JavaScript中的月份是从0开始的，所以需要+1  
          let month = ('0' + (date.getMonth() + 1)).slice(-2);
          // 同样，日期也是从1开始的，但是通常我们需要两位数，所以需要补零  
          let day = ('0' + date.getDate()).slice(-2);
          let hours = ('0' + date.getHours()).slice(-2);
          let minutes = ('0' + date.getMinutes()).slice(-2);
          let seconds = ('0' + date.getSeconds()).slice(-2);
          // 按照你需要的格式拼接这些部分  
          return `${year}-${month}-${day}T${hours}:${minutes}:${seconds}`;
        }
        return formatDate(new Date())
      }
      // 先保存，再提交
      this.$refs.customerForm.validate((valid) => {
        if(valid) {
          console.log(`form valid and submit answer`)
          let answers = this.buildAnswers()
          var saveForm = {
            tableId: this.tableId,
            answers: answers
          }
          this.saveAnswers(saveForm).then(res => {
            if(res) {
              // 提交
              var submitForm = {
                tableId: this.tableId,
                data: {
                  date: buildCreateTime()
                }
              }
              this.submitAnswers(submitForm).then(res => {
                if(res) {
                  this.$message.success('提交成功')
                  this.$router.push({ name: 'home' })
                } else {
                  this.$message.error('提交失败，请检查网络状态')
                }
              })
            } else {
              this.$message.error('保存失败，请检查网络状态')
            }
          })
        } else {
          this.$message.error('采集表不可有空栏')
        }
      })
    },
  }
}
</script>

<style>
